    --system-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    font-family: var(--system-font);
}

.prevent-select {
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background-color: #393E46;
    display: grid;
    grid-template-columns: 1fr 4fr;
}

aside {
    box-shadow: 1px 0px 0px 0px rgba(238,238,238,0.2);
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #222831;
    color: #EEEEEE;
    h2 {
        margin-bottom: 0;
        margin-top: 40px;
        font-weight: 500;
    }
    &>div {
        flex: 1;
        display: flex;
        flex-direction: column;
    }
    img {
        width: 80px;
    }
    ul {
        padding: 0;
        overflow: auto;
        flex: 1;
        list-style: none;
        font-size: 1.2rem;
        span {
            margin-right: 10px;
        }
    }
    li {
        padding: 5px 20px;
        cursor: pointer;
        margin-bottom: 10px;
        border-radius: 10px;
    }
    li:hover {
        background-color: #393E46;
    }
    button {
        font-size: 1.2rem;
        padding: 10px 0;
        background-color: #222831;
        color: #EEEEEE;
        font-family: var(--system-font);
        border: 1px solid #EEEEEE;
        border-radius: 20px;
    }
    button:hover {
        background-color: #393E46;
    }
}

header {
    display: flex;
    align-items: center;
    gap: 20px;
    h1 {
        font-size: 2rem;
        font-weight: 500;
    }
}

main {
    padding: 50px;
    overflow: auto;
    color: #EEEEEE;
    display: flex;
    &>div {
        margin: auto;
        width: 500px;   
    }
    &>div>div {
        font-size: 4rem;
        margin-bottom: 30px;
    }
    h2 {
        font-size: 2.5rem;
        margin: 0 0;
    }
}

.todo-item-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    ul {
        list-style-type: none;
        padding-left: 0;
    }
    p {
        position: relative;
        margin: 0;
        padding: 0 10px;
        width: 480px;
        text-overflow: ellipsis;
        overflow: hidden; 
        white-space: nowrap;
    }
    &:not(:last-of-type):hover {
        cursor: pointer;
        background-color: #222831;
        border-radius: 10px;
    }
    span {
        position: absolute;
        right: 10px;
        top: 5px;
        border: 1px solid #EEEEEE;
        background-color: #222831;
        border-radius: 100%;
        padding: 0 5px;
        font-size: 1rem;
    }
    span:hover {
        background-color: #393E46;
    }
    &>p {
        font-size: 1.5rem;
    }
    li {
        position: relative;
        padding-left: 20px;
        margin-top: 5px;
    }
    
    button {
        font-size: 1rem;
        padding: 5px 0;
        background-color: #222831;
        color: #EEEEEE;
        font-family: var(--system-font);
        border: 1px solid #EEEEEE;
        border-radius: 20px;
        width: 160px;
        margin-top: 20px;
    }
    button:hover {
        background-color: #393E46;
    }
}

.hidden {
    opacity: 0;
}

.marked {
    text-decoration: line-through;
    color: #eeeeee7a;
}

.todos-wrapper {
    margin-top: 60px;
    display: flex;
    gap: 20px;
    padding-left: 0px;
    flex-direction: column;
}

dialog {
    width: 400px;
    background-color: #222831;
    border: 1px solid #eeeeee69;
    border-radius: 20px;
    color: #EEEEEE;
    h2 {
        font-weight: 500;
    }
    ul {
        list-style: none;
        padding: 0;
    }
    li:not(:last-child) {
        display: flex;
        gap: 10px;
        flex-direction: column;
        margin-bottom: 20px;
    }
    li:last-child {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }
    input, textarea {
        font-family: var(--system-font);
        color: #EEEEEE;
        background-color: #393E46;
        border-radius: 10px;
        height: 30px;
        border: 1px solid #eeeeee69;
        outline: none;
        padding: 5px;
        font-size: 1rem;
        font-weight: 300;
    }
    textarea {
        resize: vertical;
        height: 90px;
    }
    input:focus, textarea:focus {
        outline: 1px solid #eeeeee;
    }
    button {
        flex: 1;
        color: #EEEEEE;
        font-family: var(--system-font);
        border: 1px solid #EEEEEE;
        border-radius: 20px;
        background-color: #222831;
        padding: 10px 0;
    }
    button:hover {
        background-color: #393E46;
    }
}

dialog::backdrop {
    backdrop-filter: blur(2px);
}
